.container{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 5px 15px;
    box-shadow: 1px 1px 5px rgb(211, 211, 211);
    margin: 20px auto;
    border-radius: 10px;
}
.vertical{
    flex-direction: column;
    align-items: flex-start !important;
}
.container img{
    margin-right: 10px;
}
.avatarName{
    width: 100%;
    font-size: 14px;
    font-weight: 700; 
}
.subTitle{
    font-size: 14px;
    font-size: rgb(200, 200, 200);
}