.fakeBox{
    display: flex;
    align-items: center;
    justify-content: center;
    width: fit-content;
    background-color: rgb(228, 228, 228);
    padding: 0px 10px;
    border-radius: 5px;
}
.fakeBox input{
    height: 100%;
    font-size: 16px;
    padding: 10px 20px;
    background-color: transparent;
    border: none;
}
.fakeBox input:focus{
    outline: none;
}