.TabContainer{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    position: fixed;
    bottom: 0px;
    left: 0px;
    width: 100%;
    height: 7vh;
    z-index: 1000;
    box-shadow: 1px 1px 20px rgb(206, 206, 206);
    background-color: white;
}
.Tab{
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
    color: grey;
}