.container{
    box-shadow: .5px .5px 2px rgb(210, 210, 210),
                -.5px -.5px 2px rgb(210, 210, 210);
    border-radius: 5px;
    padding: 10px;
    margin-top: 20px;
}

.firstContainer{
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.content{
    margin-left: 20px;
}
.content h1{
    margin-bottom: 5px !important;
    font-size: 22px;
}
.address{
    color: rgb(77, 77, 77);
    font-size: 14px;
    margin: 5px 0px;
}
.btns{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
}
.btns div:first-child{
    margin-right: 20px;
}