.container{
    width: 100%;
    padding: 20px;
}

.location p, .location div{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 7px;
}
.location div img{
    width: 15px;
    margin-right: 10px;
}
.Header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}
.listContainer{
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}
.list{
    flex-basis: 100%;
    margin-right: 10px;
}
.list:last-child{
    margin-right: 0px;
}
